.form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 30px;
}

/* стили для гадской кнопки инпута */
.input {
	outline: 0;
	border: none;
	border-bottom: 3px solid rgba(154, 160, 186, .3);
	;
	padding-bottom: 5px;
	font-family: 'Arial', sans-serif;
	font-size: 20px;
	color: #9aa0ba;
}

.input-file {
	position: relative;
	display: inline-block;
}

.input-file span {
	position: relative;
	display: inline-block;
	cursor: pointer;
	outline: none;
	text-decoration: none;
	font-size: 14px;
	vertical-align: middle;
	color: rgb(255 255 255);
	text-align: center;
	border-radius: 4px;
	background-color: #3ac7b9;
	line-height: 22px;
	height: 40px;
	padding: 10px 20px;
	box-sizing: border-box;
	border: none;
	margin: 0;
	transition: background-color 0.2s;
}

.input-file input[type=file] {
	position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;
}

/* Focus */
.input-file input[type=file]:focus+span {
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .25);
}

/* Hover/active */
.input-file:hover span {
	background-color: rgba(0, 123, 255, .25);
}

.input-file:active span {
	background-color: rgba(0, 0, 0);
	;
}

/* Disabled */
.input-file input[type=file]:disabled+span {
	background-color: #eee;
}