.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, .3);
  opacity: 0;
  pointer-events: none;
  transition: .5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  background-color: #fff;
  padding: 40px 30px;
  border-radius: 15px;
  max-width: 75vw;
  max-height: 75vh;
}